@import "common_styles/index";
@import "common_styles/initial";
@import "common_styles/initial_specified_media";

* {
    scroll-behavior: smooth;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: Poppins, sans-serif;

    &::-webkit-scrollbar {
        width: 4px;
    }

    &::-webkit-scrollbar-thumb {
        background-color: rgb(87, 92, 102);
        border-radius: 6px;
    }

    &::-webkit-scrollbar-track {
        background-color: rgb(34, 42, 53);
    }
}

#root {
    min-height: 100vh;
    overflow-x: hidden;
}

.App {
    color: #fff;
    display: flex;
    flex-direction: column;
}

main {
    margin-top: 80px;
    background-color: $color-black;
}

section {
    padding-bottom: 100px;
}

.container_box {
    max-width: 1200px;
    padding-left: 24px;
    padding-right: 24px;
    margin: 0 auto;
}

.polygon-gradient_background {
    padding-bottom: 16px;
    background: linear-gradient(38.73deg, rgba(204, 0, 187, 0.15) 0%, rgba(201, 32, 184, 0) 50%), linear-gradient(141.27deg, rgba(0, 70, 209, 0) 50%, rgba(0, 70, 209, 0.15) 100%);
    clip-path: polygon(0px 0px, 100% 0px, 100% 100%, 30% 94%, 0px 100%);
    margin-bottom: 100px;

    &:last-of-type {
        margin-bottom: 0;
        clip-path: polygon(0px 0px, 100% 0px, 100% 100%, 30% 98%, 0px 100%);
    }
}




@media screen and (min-width: $xxl-size-min-width) {
    .container_box {
        max-width: 1536px;
    }
}

@media screen and (max-width: $lg-size-max-width) {
    main {
        margin-top: 64px;
    }

    section {
        padding-bottom: 80px;
    }


    .polygon-gradient_background {
        margin-bottom: 60px;
        clip-path: polygon(0px 0px, 100% 0px, 100% 100%, 30% 96%, 0px 100%);

        &:last-of-type {
            clip-path: polygon(0px 0px, 100% 0px, 100% 100%, 30% 99%, 0px 100%);
        }
    }
}

@media screen and (max-width: $lg-size-max-width) {
    section {
        padding-bottom: 54px;
    }
}


@media screen and (max-width: $sm-size-max-width) {
    .container_box {
        padding-left: 16px;
        padding-right: 16px;
    }


    .polygon-gradient_background {
        margin-bottom: 48px;
        clip-path: polygon(0px 0px, 100% 0px, 100% 100%, 30% 97%, 0px 100%);

        &:last-of-type {
            clip-path: polygon(0px 0px, 100% 0px, 100% 100%, 30% 99.3%, 0px 100%);
        }
    }
}